<template>
    <div id="CheckBox">
        <md-radio v-for="gender in genders" v-bind:key="gender.id" :value="gender.label" v-model="sex">
            {{gender.label}}
        </md-radio>
    </div>
</template>

<script>
    const axios = require('axios').default;
    export default {
        name: "checkbox_group",
        data: () => ({
            sex: false,
            genders: {}
        }),
        mounted() {
            axios.get('./config.json')
                .then((res) => {
                    this.genders = res.data.Sex.items
                })
        }
    }
</script>

<style lang="scss" scoped>
    .md-radio {
        display: flex;
    }
</style>
