<template>
    <div>
        <md-table>
            <md-table-row>
                <md-table-head v-for="title in titles" v-bind:key=title>{{title}}</md-table-head>
            </md-table-row>

            <md-table-row v-for="(titles,key) in values" v-bind:key=key>
                <md-table-cell v-for="content in titles" v-bind:key=content>{{content}}</md-table-cell>
            </md-table-row>
        </md-table>
    </div>
</template>

<script>
    const axios = require('axios').default;
    export default {
        name: "tables",
        data: () => ({
            titles: "",
            values:""
        }),
        mounted() {
            axios.get('./config.json')
                .then((res) => {
                    this.titles = res.data.table.title
                    this.values = res.data.table.value
                })
        },
    }
</script>

<style scoped>

    .md-table {
        width: 900px;
        position: absolute;
        right: 0;
        top: 100px;
    }
</style>
