<template>
    <div id="Date">
        <md-datepicker v-model="date">
            <label>Select date</label>
        </md-datepicker>
    </div>
</template>

<script>
    let now = new Date()
    export default {
        name: "datetime_picker",
        data: () => ({
            date:now,
        }),
    }
</script>

<style lang="scss" scoped>
    .md-field{
        width: 300px;
    }
</style>
