import Vue from 'vue'
import App from './App.vue'

import datetime_picker from './components/datetime_picker'
import checkbox_group from './components/checkbox_group'
import combo_box from './components/combo_box'
import keyword from './components/keyword'
import demo from './components/demo'
import tables from './components/tables'
import member from './components/member'
import password from './components/password'
import mail from './components/mail'


import VueMaterial from 'vue-material'
import 'vue-material/dist/vue-material.min.css'
import 'vue-material/dist/theme/default.css'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import VueRouter from 'vue-router'
import ViewUI from 'view-design';
import 'view-design/dist/styles/iview.css';
import axios from 'axios'
import VueAxios from 'vue-axios'

Vue.config.productionTip = false

Vue.use(ViewUI);
Vue.use(VueMaterial)
Vue.use(VueRouter)
Vue.use(ElementUI);
Vue.use(VueAxios, axios)


Vue.config.productionTip = false
const router = new VueRouter({
  routes: [
    {path: '/datetime_picker', component:datetime_picker},
    {path: '/checkbox_group', component:checkbox_group},
    {path: '/combo_box', component:combo_box},
    {path: '/keyword', component:keyword},
    {path: '/demo', component:demo},
    {path: '/tables', component:tables},
    {path: '/member', component:member},
    {path: '/password', component:password},
    {path: '/mail', component:mail},

  ]
})

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
