<template>
    <div id="keyword">
        <md-autocomplete
                v-model="selectedEmployee"
                :md-options="keyword"
                md-layout="box"
                md-dense>
            <label>關鍵字</label>
        </md-autocomplete>

    </div>
</template>

<script>
    const axios = require('axios').default;
    export default {
        name: "keyword",
        data:()=>({
            selectedEmployee: null,
            keyword:[]

        }),
        mounted(){
            axios.get('./config.json')
                .then((res)=>{
                    this.keyword=res.data.keywords
                })
        }
    }
</script>

<style lang="scss" scoped>

    .md-field{
        width: 300px;
    }

</style>

