<template>
    <div id="ComboBox">
        <md-field>
            <label for="language"><small>{{placeholder}}</small></label>
            <md-select v-model="language" name="language" id="language">
                <md-option v-for=" item in Langs" v-bind:key="item.value" :value="item.label">{{item.label}}</md-option>
            </md-select>
        </md-field>
    </div>
</template>

<script>
    const axios = require('axios').default;
    export default {
        name: "combo_box",
        data: () => ({
            language:null,
            placeholder:"",
            Langs:{}
        }),
        mounted() {
            axios.get('./config.json')
                .then((res) => {
                    this.placeholder = res.data.language.placeholder
                    this.Langs = res.data.language.items
                })
        }
    }
</script>

<style lang="scss" scoped>

    .md-field{
        width: 300px;
    }

</style>

