<template>
    <div>
        <md-field>
            <label>{{mails.mail}}</label>
            <md-input v-model="mail"></md-input>
        </md-field>
    </div>
</template>

<script>
    const axios = require('axios').default;
    export default {
        name: "member",
        data: () => ({
            required: "",
            mail: null,
            password: null,
            mails:"",
            hasMessages: false
        }),
        mounted() {
            axios.get('./config.json')
                .then((res) => {
                    this.mails = res.data.input
                })
        },

        computed: {
            member: function () {
                var re = new RegExp('^[[:alnum:]!@#$%_&]{8,}$');
                var user = re.exec(this.required);
                return {
                    'md-invalid': this.required.length < 8 && !user
                }

            }
        }


    }
</script>

<style lang="scss" scoped>

    .md-field {
        width: 300px;
    }

</style>
