<template>
    <div>
        <md-field :class="member">
            <md-icon>account_circle</md-icon>
            <label>{{users.user}}</label>
            <md-input v-model="required" required></md-input>
            <span class="md-error">{{users.error}}</span>
        </md-field>
    </div>
</template>

<script>
    const axios = require('axios').default;
    export default {
        name: "member",
        data: () => ({
            required: "",
            mail: null,
            password: null,
            users:"",
            hasMessages: false
        }),
        mounted() {
            axios.get('./config.json')
                .then((res) => {
                    this.users = res.data.input.member
                })
        },

        computed: {
            member: function () {
                var re = new RegExp('^[[:alnum:]!@#$%_&]{8,}$');
                var user = re.exec(this.required);
                return {
                    'md-invalid': this.required.length < 8 && !user
                }

            }
        }


    }
</script>

<style lang="scss" scoped>

    .md-field {
        width: 300px;
    }

</style>
