<template>
    <div>
        <md-field :md-toggle-password="false">
            <md-icon>lock</md-icon>
            <label>{{passwords.password}}</label>
            <md-input v-model="password" type="password"></md-input>

        </md-field>
    </div>
</template>

<script>
    const axios = require('axios').default;
    export default {
        name: "member",
        data: () => ({
            required: "",
            mail: null,
            password: null,
            passwords:"",
            hasMessages: false
        }),
        mounted() {
            axios.get('./config.json')
                .then((res) => {
                    this.passwords = res.data.input
                })
        },

        computed: {
            member: function () {
                var re = new RegExp('^[[:alnum:]!@#$%_&]{8,}$');
                var user = re.exec(this.required);
                return {
                    'md-invalid': this.required.length < 8 && !user
                }

            }
        }


    }
</script>

<style lang="scss" scoped>

    .md-field {
        width: 300px;
    }

</style>
